$(document).ready(function () {

  var navoffset = 160;

  $(window).on('scroll', function () {
    var navScroll = $(this).scrollTop();

    if (navScroll >= navoffset) {
      $('.navbar').addClass('fixed'),
        $('.services').css('padding-top', '170px')
    } else {
      $('.navbar').removeClass('fixed'),
        $('.services').css('padding-top', '70px')
    }
  })


  var elem = $('.skills'),
    elemPosition = elem.offset().top - 500,
    isFill = false;

  $(window).on('scroll', function () {
    var windowScroll = $(this).scrollTop();

    if (windowScroll >= elemPosition && !isFill) {
      $('.circle').circleProgress({
        size: 120,
        thickness: 4,
        fill: {
          color: '#00a99d'
        }
      }).on('circle-animation-progress', function (event, progress, stepValue) {
        $(this).find('strong').text(String(stepValue.toFixed(2)).substr(2));
      })
      isFill = true;
    }
  });


  new WOW().init();

  $('[data-fancybox="gallery"]').fancybox({
    // Options will go here
  });

  $('.works__all').on('click', function () {
    $(this).hide()
  })

  $("#owl-1").owlCarousel({
    loop: true,
    margin: 10,
    nav: true,
    dots: false,
    navText: ["<i class='fa fa-caret-left'></i>", "<i class='fa fa-caret-right'></i>"],
    responsive: {
      0: {
        items: 1
      },
      480: {
        items: 2
      },
      768: {
        items: 3
      },
      1000: {
        items: 4
      }
    }
  });

  $("#owl-2").owlCarousel({
    loop: true,
    nav: true,
    dots: false,
    items: 1,
    navText: ["<i class='fa fa-caret-left'></i>", "<i class='fa fa-caret-right'></i>"]
  });


  $("#owl-3").owlCarousel({
    loop: true,
    margin: 20,
    nav: true,
    dots: false,
    navText: ["<i class='fa fa-caret-left'></i>", "<i class='fa fa-caret-right'></i>"],
    responsive: {
      0: {
        items: 1
      },
      480: {
        items: 2
      },
      768: {
        items: 3
      },
      1000: {
        items: 4
      }
    }
  });


})
